/* Button loader used when loading */
.lds-ellipsis {
    display: block;
    margin: auto;
    position: relative;
    width: 45px;
    height: 35px;
  }
  .lds-ellipsis div {
    position: absolute;
    top: 14px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: #fff;
    -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
            animation-timing-function: cubic-bezier(0, 1, 1, 0);
  }
  .lds-ellipsis div:nth-child(1) {
    left: 2px;
    -webkit-animation: lds-ellipsis1 0.7s infinite;
            animation: lds-ellipsis1 0.7s infinite;
  }
  .lds-ellipsis div:nth-child(2) {
    left: 2px;
    -webkit-animation: lds-ellipsis2 0.7s infinite;
            animation: lds-ellipsis2 0.7s infinite;
  }
  .lds-ellipsis div:nth-child(3) {
    left: 20px;
    -webkit-animation: lds-ellipsis2 0.7s infinite;
            animation: lds-ellipsis2 0.7s infinite;
  }
  .lds-ellipsis div:nth-child(4) {
    left: 35px;
    -webkit-animation: lds-ellipsis3 0.7s infinite;
            animation: lds-ellipsis3 0.7s infinite;
  }
  @-webkit-keyframes lds-ellipsis1 {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes lds-ellipsis1 {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @-webkit-keyframes lds-ellipsis3 {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
  }
  @keyframes lds-ellipsis3 {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
  }
  @-webkit-keyframes lds-ellipsis2 {
    0% {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
    }
    100% {
      -webkit-transform: translate(19px, 0);
              transform: translate(19px, 0);
    }
  }
  @keyframes lds-ellipsis2 {
    0% {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
    }
    100% {
      -webkit-transform: translate(19px, 0);
              transform: translate(19px, 0);
    }
  }


